(function () {
    'use strict';

    var app = angular.module('BlurAdmin.pages.snAlarm', ["ui.router"])
        .config(routeConfig);

    // angular.module('BlurAdmin.pages.snAlarm').controller('snAlarmCtl',['$scope', function ($scope) {
    //
    // }])
    /** @ngInject */
    function routeConfig($stateProvider) {

        $stateProvider
            .state('snAlarm', {
                url: '/snAlarm',
                templateUrl: 'app/pages/snAlarm/snAlarm.html',
                title: '',
                sidebarMeta: {
                    order: 800,
                    icon:'assets/img/u531.png'
                },
                controller:'snAlarmCtrl'
            });
    }

    app.factory('Resource', ['$q', '$filter', '$timeout', function ($q, $filter, $timeout) {

        //this would be the service to call your server, a standard bridge between your model an $http

        // the database (normally on your server)
        var randomsItems = [];

        function createRandomItem(id) {
            var heroes = ['Batman', 'Superman', 'Robin', 'Thor', 'Hulk', 'Niki Larson', 'Stark', 'Bob Leponge'];
            return {
                id: id,
                name: heroes[Math.floor(Math.random() * 7)],
                age: Math.floor(Math.random() * 1000),
                saved: Math.floor(Math.random() * 10000)
            };

        }

        for (var i = 0; i < 1000; i++) {
            randomsItems.push(createRandomItem(i));
        }


        //fake call to the server, normally this service would serialize table state to send it to the server (with query parameters for example) and parse the response
        //in our case, it actually performs the logic which would happened in the server
        function getPage(start, number, params) {

            var deferred = $q.defer();

            var filtered = params.search.predicateObject ? $filter('filter')(randomsItems, params.search.predicateObject) : randomsItems;

            if (params.sort.predicate) {
                filtered = $filter('orderBy')(filtered, params.sort.predicate, params.sort.reverse);
            }

            var result = filtered.slice(start, start + number);

            $timeout(function () {
                //note, the server passes the information about the data set size
                deferred.resolve({
                    data: result,
                    numberOfPages: Math.ceil(filtered.length / number)
                });
            }, 100);


            return deferred.promise;
        }

        return {
            getPage: getPage
        };

    }]);

})();
