        function convertTableData(data, tableState){
            var tableData = [];
            for(var i=0;i<data.length;i++){
                var oneData = data[i];
                tableData.push({
                    id:oneData.id,
                    warehouse:$scope.slectedItem.label,
                    requirementName:$scope.slectedMetricItem.label,
                    count:oneData.value0,
                    pickTime:dateFtt("yyyy-MM-dd hh:mm:ss", new Date(oneData.time))
                })
            }

            $scope.$apply(function () {
                $scope.smartTableData = tableData;
            })
        }

