(function () {
  'use strict';

  angular.module('BlurAdmin.theme')
    .directive('ionSlider', ionSlider);

  /** @ngInject */
  function ionSlider($timeout) {
    return {
      restrict: 'EA',
      template: '<div></div>',
      replace: true,
      scope: {
        min: '=',
        max: '=',
        type: '@',
        prefix: '@',
        maxPostfix: '@',
        prettify: '=',
        prettifySeparator: '@',
        grid: '=',
        gridMargin: '@',
        postfix: '@',
        step: '@',
        hideMinMax: '@',
        hideFromTo: '@',
        from: '=',
        to: '=',
        disable: '=',
        onChange: '=',
        onFinish: '=',
        values: '=',
        timeout: '@'
      },
      link: function ($scope, $element) {
            $element.ionRangeSlider({
              min: $scope.min,
              max: $scope.max,
              type: $scope.type,
              prefix: $scope.prefix,
              maxPostfix: $scope.maxPostfix,
              prettify_enabled: $scope.prettify,
              prettify_separator: $scope.prettifySeparator,
              grid: $scope.grid,
              gridMargin: $scope.gridMargin,
              postfix: $scope.postfix,
              step: $scope.step,
              hideMinMax: $scope.hideMinMax,
              hideFromTo: $scope.hideFromTo,
              from: $scope.from,
              to: $scope.to,
              disable: $scope.disable,
              onChange: $scope.onChange,
              onFinish: $scope.onFinish,
              values: $scope.values
            });

            $scope.$watch('min', function (value) {
              $timeout(function () {
                $element.data("ionRangeSlider").update({min: value});
              });
            }, true);
            $scope.$watch('max', function (value) {
              $timeout(function () {
                $element.data("ionRangeSlider").update({max: value});
              });
            });
            $scope.$watch('from', function (value) {
              $timeout(function () {
                $element.data("ionRangeSlider").update({from: value});
              });
            });
            $scope.$watch('to', function (value) {
              $timeout(function () {
                $element.data("ionRangeSlider").update({to: value});
              });
            });
            $scope.$watch('disable', function (value) {
              $timeout(function () {
                $element.data("ionRangeSlider").update({disable: value});
              });
            });
      }
    };
  }

})();
(function () {
    angular.module('BlurAdmin.theme')
        .directive('ngEcharts', [function () {
            return {
                replace:true,
                link: function (scope, element, attrs, ctrl) {
                    function refreshChart() {
                        var theme = (scope.config && scope.config.theme)
                            ? scope.config.theme : 'default';
                        var chart = echarts.init(element[0], 'default');
                        if (scope.config && scope.config.dataLoaded === false) {
                            chart.showLoading();
                        }

                        if (scope.config && scope.config.dataLoaded) {
                            chart.setOption(scope.option);
                            chart.resize();
                            chart.hideLoading();
                        }

                        if (scope.config && scope.config.event) {
                            if (angular.isArray(scope.config.event)) {
                                angular.forEach(scope.config.event, function (value, key) {
                                    for (var e in value) {
                                        chart.on(e, value[e]);
                                    }
                                });
                            }
                        }
                    };

                    // - config
                    // event 
                    // theme 
                    // dataLoaded 

                    scope.$watch(
                        function () {
                            return scope.config;
                        },
                        function (value) {
                            if (value) {
                                // refreshChart();
                            }
                        },
                        true
                    );

                    //option
                    scope.$watch(
                        function () {
                            return scope.option;
                        },
                        function (value) {
                            if (value) {
                                refreshChart();
                            }
                        },
                        true
                    );
                },
                scope: {
                    option: '=ecOption',
                    config: '=ecConfig'
                },
                restrict: 'EA'
            }
        }]);
})();


