(function () {
    'use strict';

    angular.module('BlurAdmin.pages.snDashboard')
        .controller('snDashboardMachinenumCtrl', DashboardMachinenumCtrl);

    /** @ngInject */
    function DashboardMachinenumCtrl($scope) {

        $.ajax({
            type: "GET",
            url: "/v1/device/info",
            async: true,
            dataType: "json",
            success: function (data) {
                constructData(data)
            }
        });

        function constructData(data) {
            var color = ['#48aaee', '#99da7b', '#fed96c', '#f4857c', '#8898e6', '#ff5b00', '#ff3000'];
            var serieseData = [];
            var titlenumber;
            var legendData = [];

            var i = 0;
            for (var key in data) {
                if (key !== "totalNum") {

                    serieseData.push({
                        value: data[key], name: key + data[key] + '', itemStyle: {
                            normal: {
                                borderWidth: 5,
                                // shadowBlur: 20,
                                borderColor: color[i],
                                shadowColor: color[i]
                            }
                        }
                    })
                    i++;

                    legendData.push({
                        name: key + data[key] + '', icon: 'circle'
                    })
                } else {
                    titlenumber = data[key];
                }
            }


            var option = {
                color: color,
                title: {
                    text: titlenumber,
                    subtext: '()',
                    left: 68,
                    top: 60,
                    textStyle: {
                        color: '#fff',
                        fontSize: 32
                    },
                    subtextStyle: {
                        color: '#fff'
                    }
                },
                tooltip: {
                    trigger: 'item',
                    formatter: "{a} <br/>{b}: {c} ({d}%)"
                },
                legend: {
                    orient: 'auto',
                    itemHeight: 10,
                    x: 'right',
                    data: legendData,
                    top: 10,
                    right: '20%',
                    left: 200,
                    textStyle: {
                        color: '#fff',
                        fontSize: 12
                    }
                },
                grid: {
                    left: '10%',
                    width: 100
                },
                series: [
                    {
                        name: '',
                        type: 'pie',
                        radius: [55, 70],
                        center: ['25%', '50%'],
                        avoidLabelOverlap: false,
                        label: {
                            normal: {
                                show: false,
                                position: 'center'
                            },
                            emphasis: {
                                show: false,
                                textStyle: {
                                    fontSize: '30',
                                    fontWeight: 'bold'
                                }
                            }
                        },
                        labelLine: {
                            normal: {
                                show: false
                            }
                        },
                        data: serieseData
                    }
                ]
            };

            $scope.machineConfig = {
                theme: 'dark',
                dataLoaded: true
            };
            $scope.machineOption = option;
        }


    }
})();
