(function () {
    'use strict';

    angular.module('BlurAdmin.pages.snAlarm')
        .controller('almTableCtrl', almTableCtrl);

    /** @ngInject */
    function almTableCtrl($scope, $filter, editableOptions, editableThemes) {

        $scope.smartTablePageSize = 12;

        $scope.onAlmConfirmClk = function () {
            var checkedArr = [];
            var alertId = [];
            for (var i =0,len=$scope.smartTableData.length;i<len;i++){
                var item = $scope.smartTableData[i];
                if(item.checked){
                    checkedArr.push(item);
                    alertId.push({
                        alertId:item.id,
                        createTime:item.occurTime
                    });
                }
            }
            // console.log(checkedArr);

            $.ajax({
                type: 'POST',
                url: '/v1/alert/confirm',
                data: JSON.stringify(alertId),
                success: function (data) {
                    // console.log(data);
                    refreshTable($scope.curPageIndex, $scope.pageNum)
                },
                dataType: "json",
                contentType: "application/json; charset=utf-8",
            });
        }


        $scope.onAlmClearClk = function () {
            var checkedArr = [];
            var alertId = [];
            for (var i =0,len=$scope.smartTableData.length;i<len;i++){
                var item = $scope.smartTableData[i];
                if(item.checked){
                    checkedArr.push(item);
                    alertId.push({
                        alertId:item.id,
                        createTime:item.occurTime
                    });
                }
            }
            // console.log(checkedArr);

            $.ajax({
                type: 'POST',
                url: '/v1/alert/clear',
                data: JSON.stringify(alertId),
                success: function (data) {
                    // console.log(data);
                    refreshTable($scope.curPageIndex, $scope.pageNum)
                },
                dataType: "json",
                contentType: "application/json; charset=utf-8",
            });
        }

        function refreshTable(curIndex, pageSize){
            $.ajax({
                type: "GET",
                url: "/v1/alert/list?curIndex=" + curIndex + '&pageSize=' + pageSize + '&status=0',
                async: true,
                dataType: "json",
                success: function (data) {
                    converData(data);
                }
            });
        }

        function converData(data){
            for(var i=0;i<data.length;i++){
                var oneData = data[i];
                oneData.occurTimeText = dateFtt("yyyy-MM-dd hh:mm:ss", new Date(oneData.occurTime));

                if(oneData.clearTime === 0){
                    oneData.clearTimeText = '---';
                }
            }
            $scope.$apply(function () {
                // data = [];
                $scope.smartTableData = data;
                $scope.isTableEmpty = data.length === 0;
            })
        }

        $scope.allChecked = false;
        $scope.onAllCheckChange = function(allChecked){
            for(var i=0;i<$scope.smartTableData.length;i++){
                $scope.smartTableData[i].checked = allChecked;
            }
        };

        $scope.onItemCheckChange = function(itemChecked){
            if(!itemChecked){
                $scope.allChecked = false;
            }
        }

        $scope.callServer = function (tableState) {
            var pagination = tableState.pagination;
            var start = pagination.start || 0;     //
            var number = pagination.number || 12;  // 
            $scope.curPageIndex = start/number;
            $scope.pageNum = number;
            refreshTable(start/number, number);
            tableState.pagination.numberOfPages = 10;
        }

    }
})();
